<template>
    <div>
        <el-row :gutter="10">
            <el-col :span="12">
                <el-card shadow="hover" class="mgb20" style="height:280px;">
                    <div class="user-info">
                        <img src="../../assets/images/user.jpg" class="user-avator" alt/>
                        <div class="user-info-cont">
                            <div class="user-info-name">hello！{{this.userInfo.real_name}}</div>
                            <!--                            <div>role</div>-->
                        </div>
                    </div>
                    <div class="user-info-list">
                        用户身份：
                        <span>{{this.userInfo.department}}-{{this.userInfo.real_name}}</span>
                    </div>
                    <!--                    <div class="user-info-list">-->
                    <!--                        上次登录地点：-->
                    <!--                        <span>东莞</span>-->
                    <!--                    </div>-->
                </el-card>
            </el-col>
            <el-col :span="12">
                <el-card shadow="hover" style="height:280px;">
                    <div slot="header" class="clearfix">
                        <div>
                            <span>账户详情</span>
                            <a href="javascript:;" style="margin-left: 700px" @click="SetPassword">修改密码</a>
                        </div>

                        <!--                        <el-button type="text" style="display: inline-block;margin-left: 630px">修改密码</el-button>-->
                    </div>
                    <div style="font-size: 20px;line-height: 40px">
                        <div style="margin-left: 100px">用户名：<span style="margin-left: 150px;color: #2d8cf0">{{this.userInfo.username}}</span>
                        </div>
                        <div style="margin-left: 100px">姓名：<span style="margin-left: 150px;color: #2d8cf0">{{this.userInfo.real_name}}</span>
                        </div>
                        <div style="margin-left: 100px">部门：<span style="margin-left: 150px;color: #2d8cf0">{{this.userInfo.department}}</span>
                        </div>
                        <div style="margin-left: 100px">密码：<span style="margin-left: 150px;color: #2d8cf0">{{this.user.password}}</span>
                        </div>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-row :gutter="20" class="mgb20">
            <el-col :span="6">
                <el-card shadow="hover" :body-style="{padding: '0px'}">
                    <div class="grid-content grid-con-1">
                        <i class="el-icon-lx-people grid-con-icon"></i>
                        <div class="grid-cont-right">
                            <div class="grid-num">{{this.NumInfo.AllContract}}</div>
                            <div>总合同</div>
                        </div>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="hover" :body-style="{padding: '0px'}">
                    <div class="grid-content grid-con-2">
                        <i class="el-icon-lx-notice grid-con-icon"></i>
                        <div class="grid-cont-right">
                            <div class="grid-num">{{this.NumInfo.IsEffectContract}}</div>
                            <div>生效合同</div>
                        </div>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="hover" :body-style="{padding: '0px'}">
                    <div class="grid-content grid-con-3">
                        <i class="el-icon-lx-goods grid-con-icon"></i>
                        <div class="grid-cont-right">
                            <div class="grid-num">{{this.NumInfo.NoEffectContract}}</div>
                            <div>失效合同</div>
                        </div>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="hover" :body-style="{padding: '0px'}">
                    <div class="grid-content grid-con-3">
                        <i class="el-icon-lx-goods grid-con-icon"></i>
                        <div class="grid-cont-right">
                            <div class="grid-num">{{this.NumInfo.AllWebsite}}</div>
                            <div>拥有网店代理商数量</div>
                        </div>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-row :gutter="20" class="mgb20">
            <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
                <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px"
                         class="demo-ruleForm">
                    <el-form-item label="原有密码" prop="oldPass">
                        <el-input v-model.number="ruleForm.oldPass"></el-input>
                    </el-form-item>
                    <el-form-item label="新密码" prop="pass">
                        <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="checkPass">
                        <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
                    </el-form-item>
                    <!--                <el-form-item>-->
                    <!--                    <el-button type="primary" @click="submitForm('ruleForm')">确认修改</el-button>-->
                    <!--                </el-form-item>-->
                </el-form>
                <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit('ruleForm')">确 定</el-button>
            </span>
            </el-dialog>
        </el-row>
    </div>
</template>

<script>
    import bus from './../../components/bus.js';
    import {mapState, mapActions} from 'vuex';
    import {SetPassword,NumInfo} from './../../server/index'

    export default {
        name: 'dashboard',
        data() {
            var validateoldPass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入原密码'));
                } else {
                    if (this.ruleForm.oldpass !== '') {
                        this.$refs.ruleForm.validateField('oldPass');
                    }
                    callback();
                }
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.ruleForm.pass) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                // name: localStorage.getItem('ms_username'),
                name: '',
                user: {
                    password: '********'
                },
                NumInfo: {
                    AllContract: '',
                    IsEffectContract: '',
                    NoEffectContract: '',
                    AllWebsite: ''
                },
                ruleForm: {
                    pass: '',
                    checkPass: '',
                    oldPass: '',
                },
                rules: {
                    pass: [
                        {validator: validatePass, trigger: 'blur'}
                    ],
                    checkPass: [
                        {validator: validatePass2, trigger: 'blur'}
                    ],
                    age: [
                        {validator: validateoldPass, trigger: 'blur'}
                    ]
                },
                todoList: [],
                editVisible: false,
            };
        },
        components: {},
        computed: {
            ...mapState(['userInfo'])
        },
        mounted(){
            this.GetNumInfo()
        },
        methods: {
            //1、获取数量的相关数据
            async GetNumInfo(){
                let result = await NumInfo(this.userInfo.username)
                if(result.statusCode===200){
                    this.NumInfo.AllContract = result.data.AllContract
                    this.NumInfo.IsEffectContract = result.data.IsEffectContract
                    this.NumInfo.NoEffectContract = result.data.NoEffectContract
                    this.NumInfo.AllWebsite = result.data.AllWebsite
                }
            },
            //2、修改密码
            SetPassword() {
                this.editVisible = true;
            },
            saveEdit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$confirm('确定要修改吗？', '提示', {
                            type: 'warning'
                        }).then(async () => {
                            let result = await SetPassword(this.ruleForm, this.user.username);
                            if (result.statusCode === 200) {
                                this.$message.success(result.message);
                                this.editVisible = false;
                            } else {
                                this.$message.error(result.message);
                            }
                        }).catch(() => {
                        });
                    } else {
                        console.log('提交失败!!');
                        return false;
                    }
                });
            }
        }
    };
</script>


<style scoped>
    .el-row {
        margin-bottom: 20px;
    }

    .grid-content {
        display: flex;
        align-items: center;
        height: 100px;
    }

    .grid-cont-right {
        flex: 1;
        text-align: center;
        font-size: 14px;
        color: #999;
    }

    .grid-num {
        font-size: 30px;
        font-weight: bold;
    }

    .grid-con-icon {
        font-size: 50px;
        width: 100px;
        height: 100px;
        text-align: center;
        line-height: 100px;
        color: #fff;
    }

    .grid-con-1 .grid-con-icon {
        background: rgb(45, 140, 240);
    }

    .grid-con-1 .grid-num {
        color: rgb(45, 140, 240);
    }

    .grid-con-2 .grid-con-icon {
        background: rgb(100, 213, 114);
    }

    .grid-con-2 .grid-num {
        color: rgb(45, 140, 240);
    }

    .grid-con-3 .grid-con-icon {
        background: rgb(242, 94, 67);
    }

    .grid-con-3 .grid-num {
        color: rgb(242, 94, 67);
    }

    .user-info {
        display: flex;
        align-items: center;
        padding-bottom: 20px;
        border-bottom: 2px solid #ccc;
        margin-bottom: 20px;
    }

    .user-avator {
        width: 150px;
        height: 150px;
        border-radius: 50%;
    }

    .user-info-cont {
        padding-left: 50px;
        flex: 1;
        font-size: 14px;
        color: #999;
    }

    .user-info-cont div:first-child {
        font-size: 30px;
        color: #222;
    }

    .user-info-list {
        font-size: 14px;
        color: #999;
        line-height: 25px;
    }

    .user-info-list span {
        margin-left: 70px;
    }

    .mgb20 {
        margin-bottom: 20px;
    }

    .todo-item {
        font-size: 14px;
    }

    .todo-item-del {
        text-decoration: line-through;
        color: #999;
    }
</style>
